@import url(https://fonts.googleapis.com/css?family=Courgette);
@import url(https://fonts.googleapis.com/css?family=Crimson+Text);
html,body{margin:0px;padding:0px;height:100%;width:100%;overflow:hidden}body{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:15px}body.active{background-color:#121212}

.home{width:1030px;padding:10px}.home .info{position:relative}.home .info .avatar{float:right;margin-right:59px}.home .info .avatar img{width:94px;height:90px;border-radius:12px;box-shadow:0 3px 13px rgba(0,0,0,0.15)}.home .info .avatar:hover{-webkit-animation:jiggle 1.4s ease-in-out;animation:jiggle 1.4s ease-in-out}@-webkit-keyframes jiggle{48%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}50%{-webkit-transform:scale(1.2, 0.8);transform:scale(1.2, 0.8)}56%{-webkit-transform:scale(0.8, 1.2) translate(0, -5px);transform:scale(0.8, 1.2) translate(0, -5px)}59%{-webkit-transform:scale(1, 1) translate(0, -3px);transform:scale(1, 1) translate(0, -3px)}}@keyframes jiggle{48%{-webkit-transform:scale(1, 1);transform:scale(1, 1)}50%{-webkit-transform:scale(1.2, 0.8);transform:scale(1.2, 0.8)}56%{-webkit-transform:scale(0.8, 1.2) translate(0, -5px);transform:scale(0.8, 1.2) translate(0, -5px)}59%{-webkit-transform:scale(1, 1) translate(0, -3px);transform:scale(1, 1) translate(0, -3px)}}.home .info .name{margin-top:40px;margin-bottom:30px;font-family:'Courgette', cursive;letter-spacing:0;color:#2b2928;text-shadow:none;font-size:3rem}.home .info .text{text-shadow:none;color:#2b2928;font-size:1.6rem;line-height:1.5rem;max-width:960px}.home .info .text .position-title{color:#ff6f5e;font-weight:bold;text-align:center}.home .info .text p{font-size:1.2rem;line-height:1.9rem}.home .info .social{overflow:hidden}.home .info .social li{list-style-type:none}.home .info .social li a{margin-left:25px;float:left;color:#1d1d1d;text-decoration:none;cursor:pointer}.home .info .social li a:hover{color:#f45905}.home .info .social .side-project{float:right;margin-right:60px;font-family:'Courgette', cursive;color:#2b2928;font-size:1.3rem}.home .react-rotating-text-cursor{color:#ffddcc}.home hr{margin:1.9em auto;width:240px;max-width:100%;border:none;border-top:1px solid #d3d3d3;border-color:rgba(150,150,150,0.2)}.home .display-project{margin-top:16px}.home .display-project .card-list{overflow:hidden;padding:10px}.home .display-project .card-list .card{float:left;margin-right:37px;margin-bottom:32px}.home .display-project .card-list .card img{width:290px;height:160px;border-radius:10px;box-shadow:0 5px 15px rgba(0,0,0,0.15);-webkit-transition:all 0.8s;transition:all 0.8s}.home .display-project .card-list .card img:hover{-webkit-transform:translateY(-8px);transform:translateY(-8px)}

.main{margin:50px auto auto auto;width:1030px;height:100vh}

